/* stylelint-disable selector-class-pattern */
.pf-c-page__main-section.rhddx-background-image {
  background-color: transparent;

  .pf-c-content {
    color: var(--text-color-white);
    text-align: center;
  }
}

.rhddx-background-image {
  /* stylelint-disable */
  --rhddx-c-image--BackgroundColor: var(--text-color-normal);
  --rhddx-c-image--BackgroundImage: url("assets/img/rhddxbg_576.jpg");
  --rhddx-c-image--Background-2x: url("assets/img/rhddxbg_576@2x.jpg");
  --rhddx-c-image--Background--sm: url("assets/img/rhddxbg_768.jpg");
  --rhddx-c-image--Background--sm-2x: url("assets/img/rhddxbg_768@2x.jpg");
  --rhddx-c-image--Background--lg: url("assets/img/rhddxbg_1200.jpg");
  --rhddx-c-image--Background--xl: url("assets/img/rhddxbg_2000.jpg");
  /* stylelint-enable */

  &::before {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;

    @media (max-width: 767.98px) {
      height: 100%;
    }

    content: "";
    background-color: var(--rhddx-c-image--BackgroundColor);
    background-repeat: no-repeat;
    background-size: cover;

    /* stylelint-disable media-feature-name-no-vendor-prefix */
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: var(--rhddx-c-image--Background-2x);
    }

    @media (min-width: 767.98px) {
      background-image: var(--rhddx-c-image--Background--sm);
    }

    @media (min-width: 991.98px) and (-webkit-min-device-pixel-ratio: 2),
      (min-width: 991.98px) and (min-resolution: 192dpi) {
      background-image: var(--rhddx-c-image--Background--sm-2x);
    }

    @media (min-width: 1199.98px) {
      background-image: var(--rhddx-c-image--Background--lg);
    }

    @media (min-width: 1200px) {
      background-image: var(--rhddx-c-image--Background--xl);
    }
  }
}
